.updateOrderForm {
    margin: 5vmax 0;
    padding: 3vmax;
    background-color: white;

}


.updateOrderForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}

.updateOrderForm>div>select {
    padding: 1vmax 4vmax;
    margin: 2rem 0;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax ;
    outline: none;
}

.updateOrderForm>div>svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}

@media screen and (max-width: 600px) {
    .newroductContainer{
        height:100% !important;

    }
    .updateOrderForm {
        padding: 3vmax  3vmax;
    
         
    }
  .updateOrderForm>h1{
    font-size: 20px;
 
  }
    .updateOrderForm>div>select {
        padding: 2.5vmax 5vmax;
        font: 300 1.7vmax ;
    }

    .updateOrderForm>div>svg {
        font-size: 2.8vmax;
    }
}