.sidebar {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    padding: 5rem 2rem;
   height: 100vh;
   

}

.sidebar>a:first-child {
    padding: 0;
}

.sidebar a {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.493);
    font: 200 1rem "Roboto";
    padding: 2rem;
    transition: all 0.5s;
}

.sidebar a:hover {
    color: rgb(12, 112, 213);
    transform: scale(1.1);
}

.sidebar a>P {
    display: flex;
    align-items: center;
}

.sidebar a>p>svg {
    margin-right: 0.5rem;
}

@media screen and (max-width: 600px) {
    .sidebar{
        height: 43vh;
    }
}