.profileContainer {
    display: flex;
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    background-color: white;
    padding-top: 60px;
  
}

.profileContainer>div {
    height: 100vh;
    display: flex;
    max-width: 100%;
    width: 100vw;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.profileContainer>div:first-child>img {
    width: 20vmax;
    border-radius: 100%;
    transition: all 0.5s;
}

.profileContainer>div:first-child>img:hover {
    transform: scale(1.05);
}

.profileContainer>div:first-child>a {
    border: none;
    font: 400 1vmax "Roboto";
    background-color: crimson;
    color: white;
    margin: 4vmax;
    text-align: center;
    transition: all 0.5s;
    width: 30%;
    padding: 0.5vmax;
    text-decoration: none;
}

.profileContainer>div:first-child>a:hover {
    background-color: darkred;
}

.profileContainer>div:last-child {
    justify-content: space-evenly;
    box-sizing: border-box;
    padding: 5vmax;
    align-items: flex-start;
}

.profileContainer>div:last-child>div>h4 {
    color: darkslategray;
    font: 400 1.2vmax "Roboto";
}

.profileContainer>div:last-child>div>p {
    color: gray;
    margin: 0.2vmax;
    font: 400 1vmax ;
}

.profileContainer>div:last-child>div:last-child {
    display: flex;
    flex-direction: column;
    width: 60%;
}

.profileContainer>div:last-child>div:last-child>a {
    border: none;
    font: 400 1vmax "Roboto";
    background-color: black;
    color: white;
    margin: 1vmax 0;
    text-align: center;
    transition: all 0.5s;
    padding: 0.5vmax;
    text-decoration: none;
}

.profileContainer>div:last-child>div:last-child>a:hover {
    background-color: darkslategray;
}


@media screen and (max-width: 600px) {

    .profileContainer {
        flex-direction: column;
        margin-top: 60px;
    }

    .profileContainer>div:first-child>h1 {
        font: 500 3.2vmax "Roboto";
        transform: translateY(-2vmax);
    }

    .profileContainer>div:first-child>a {
        font: 400 1.7vmax "Roboto";
        padding: 1vmax;
    }

    .profileContainer>div:last-child {
        text-align: center;
        align-items: center;
    }


    .profileContainer>div:last-child>div>h4 {
        font: 400 2.5vmax "Roboto";
    }

    .profileContainer>div:last-child>div>p {
        font: 400 2vmax ;
        margin: 0.5vmax 0;
    }

    .profileContainer>div:last-child>div:last-child>a {
        font: 400 1.8vmax "Roboto";
        padding: 1vmax;
        margin: 2vmax 0;
    }

}